import React from 'react';
import styled from 'styled-components';
import desktime from '../Resource/Image/Projects/Desktime project.png';
import expedia from '../Resource/Image/Projects/expedia project.png';
import timely from '../Resource/Image/Projects/timely project 2.png';
import zara from '../Resource/Image/Projects/zara project image.png';
import GitHubIcon from '@mui/icons-material/GitHub';
import StreamIcon from '@mui/icons-material/Stream';
import { useState } from 'react';
import { Link } from "@chakra-ui/react";
import TextButton from './TextButton';


const Projects = () => {


    const [state, setState] = useState('')
    const projects = [
        {
            id: 1,
            title: 'Zara',
            img: zara,
            desc: "It is an e-commerce website where users can buy clothes, accessories, shoes etc. This website is famous for it's brand name and quality. ZARA is a Spanish multi-national retail clothing chain. It specialises in fast fashion, and sells clothing, accessories, shoes, beauty products and perfumes. The head office is in Arteixo, in A Coruña in Galicia. It is the largest constituent company of the Inditex group.",
            worked: 'A collaborative project, built in one week by a team of 3 developers .',
            githubLink: 'https://github.com/uttamkrshaw/sore-sugar-4125',
            deployedLink: "https://bespoke-sable-8482c7.netlify.app/",
            tags: ['HTML', 'CSS', 'Javascript']
        },
        {
            id: 2,
            title: 'DeskTime',
            img: desktime,
            desc: 'This is a time tracking app where users can track their productive and unproductive time. DeskTime lets you do a lot more than just track time. With all the functionality this time tracking app for desktops offers, it is currently one of the most popular productivity apps in the market.',
            worked: 'An Individual project, built in one week.',
          
            githubLink: 'https://github.com/sainRaghuveer/remarkable-pin-1708',
            deployedLink: 'https://meek-meringue-41bc7b.netlify.app/',
            tags: ['HTML', 'CSS', 'Javascript', 'React', 'Chakra-UI']
        },
        {
            id: 3,
            title: 'Expedia',
            img: expedia,
            desc: "It is an online travel booking website where users can book hotel, flight, car etc. This site also provide special packages to users.",
            worked: 'A collaborative project, built in one week by a team of 5 members.',
           
            githubLink: 'https://github.com/Satyaranjan8414/upbeat-afterthought-8396',
            deployedLink: "https://upbeat-afterthought-8396-psi.vercel.app/",
            tags: ['HTML', 'CSS', 'Javascript', 'React', 'Chakra-UI']
        },
        {
          id: 4,
          title: 'Timely App',
          img: timely,
          desc: "This is a time tracking app where users can track their productive and unproductive time. Here companies can track their employees time and make thm consistent.",
          worked: 'An individual project, built in one week.',
         
          githubLink: 'https://github.com/sainRaghuveer/-exultant-fuel-3695',
          deployedLink: "https://classy-kelpie-bdc0bb.netlify.app/",
          tags: ['HTML', 'CSS', 'Javascript', 'React', 'Chakra-UI']
      },
    ]

    return (
        <Container id='projects' className='project-card'>
            <TextButton text={"Projects"}/>
            <div className='projectsContainer'>
                {projects.map((ele, index) => (
                    <div key={index} className="box">
                        <div className='imgBox'>
                            <img src={ele.img} alt={ele.title} width='120%' height='100%' />
                        </div>
                        <div className='ContentBox'>
                            <h1>{ele.title}</h1>
                            <p>{ele.desc}</p>
                            <p style={{ fontWeight: 'bold' }}>{ele.worked}</p>
                            <div className='tagsBox'>
                                {ele.tags.map((tag, index) => (
                                    <span key={index}>{tag}</span>
                                ))}
                            </div>
                            <div className='linkSection'>
                                <Link href={ele.githubLink} target="_blank">
                                <button><GitHubIcon />Github</button>

                                </Link>
                                <Link href={ele.deployedLink} target="_blank" >
                                <button><StreamIcon />Live</button>
                                    
                                </Link>
                            </div>
                        </div>
                    </div>
                ))}
            </div>
        </Container >
    )
}


const Container = styled.div`
    width:100%;
    margin:auto;
    display:flex;
    flex-direction:column;
    gap:20px;
    
    h1{
        color:teal;
        text-transform:uppercase;
    }

    .projectsContainer{
        width:80%;
        margin:auto;
        display:flex;
        align-items:center;
        flex-direction:column;
    }

    .box{
        position:relative;
        width:100%;
        height:450px;
        display:flex;
        gap:10px;
        margin-bottom:80px;
        border: 10px solid #57a5e4;
        border-radius: 20px;
        overflow:hidden;
    }

    .imgBox{
        width:50%;
        overflow:hidden;
    }

    
    .imgBox>img{
        display: block;
        transition: transform .4s; 
    }

    .imgBox>img:hover{
        position:absolute;
        width:80%;
        transform: scale(1.3);
        transform-origin: 0% 0%;
    }

    
    .ContentBox{
        width:50%;
        text-align:left;
        padding:0px 20px 20px 20px;
        
    }

    .ContentBox>h1{
        color:#343a40;
    }

    .ContentBox>p{
        width:90%;
        font-size:large;
        margin-top:0px;
    }

    .tagsBox{
        width:80%;
        display:flex;
        align-items:center;
        flex-wrap: wrap;
        line-height:20px;
        gap:5px;
        margin-bottom:20px;
    }

    .tagsBox>span{
        color:white;
        background-color:#57a5e4;
        padding: 2px 15px 2px 15px;
        border-radius:10px;
        font-size:13px;
        font-weight:bold;
    }


    .linkSection{
        display:flex;
        gap:20px;
    }

    .linkSection>a{
        text-decoration:none;
    }
    .linkSection>a:first-child>button{
        background-color:#4a5568;
        color: White;
        padding:8px 20px 8px 20px;
        border-radius:8px;
        font-weight:bold;
        display:flex;
        align-items:center;
        justify-content:center;
        gap:10px;
        border:none;
        cursor:pointer;
    }

    button{
        cursor:pointer;
    }

    button:hover{
        -webkit-transform: translateY(-2px);
        transform: translateY(-2px);
        -webkit-transition: all 0.5s;
        transition: all 0.5s;
    }
    

    .linkSection>a:last-child>button{
        background-color:#57a5e4;
        color: White;
        padding:8px 20px 8px 20px;
        border-radius:8px;
        border:none;
        font-weight:bold;
        display:flex;
        align-items:center;
        justify-content:center;
        gap:10px;
    }

    @media only screen and (min-width: 768px) and (max-width:1120px){
        .box{
            flex-direction:column;
            align-items:center;
            height:100%;
        }
 
        .projectsContainer{
            width:80%;
            margin:auto;
            
        }
        
        .imgBox{
            width:100%;
            height:400px;
        }

        .imgBox>img{
            width:100%;
        }

        .ContentBox{
            width:80%;
            display:flex;
            align-items:center;
            justify-content:center;
            flex-direction:column;
        }

        .ContentBox>h1{
            font-size:x-large;
        }

        .ContentBox>p{
            font-size:15px;
        }

        .tagsBox{
            width:100%;
            justify-content:center;
        }
    }

    @media only screen and (min-width: 481px) and (max-width:768px){
        .box{
           flex-direction:column;
           align-items:center;
           height:100%;
        }

        .projectsContainer{
            width:80%;
            margin:auto;
            
        }
        
        .imgBox{
            width:100%;
            height:280px;
        }

        .imgBox>img{
            width:100%;
        }

        .ContentBox{
            width:80%;
            display:flex;
            align-items:center;
            justify-content:center;
            flex-direction:column;
            text-align:center;
        }

        .ContentBox>h1{
            font-size:x-large;
        }

        .ContentBox>p{
            font-size:15px;
        }

        .tagsBox{
            width:100%;
            justify-content:center;
        }
    }


    @media only screen and (min-width:320px) and (max-width:480px){
        .box{
            flex-direction:column;
            align-items:center;
            height:100%;
        }

        .projectsContainer{
            width:90%;
            margin:auto;
            
        }

        .imgBox{
            width:100%;
            height:200px;
        }

        .imgBox>img{
            width:100%;
        }

        .ContentBox{
            width:80%;
            display:flex;
            align-items:center;
            justify-content:center;
            flex-direction:column;
            text-align:center;
        }

        .ContentBox>h1{
            font-size:large;
        }

        .ContentBox>p{
            font-size:13px;
            
        }


        .tagsBox{
            width:100%;
            justify-content:center;
        }
    }

    @media only screen and (max-width: 319px){
        .box{
            flex-direction:column;
            align-items:center;
            height:100%;
        }

        .projectsContainer{
            width:90%;
            margin:auto;
            
        }

        .imgBox{
            width:100%;
            height:150px;
        }

        .imgBox>img{
            width:100%;
            
        }

        .ContentBox{
            width:80%;
            display:flex;
            align-items:center;
            justify-content:center;
            flex-direction:column;
            text-align:center;
        }

        .ContentBox>h1{
            font-size:large;
        }

        .ContentBox>p{
            font-size:13px;
        }


        .tagsBox{
            width:100%;
            justify-content:center;
        }
    }
`
export default Projects